import { createRouter, createWebHashHistory } from 'vue-router'
import { userInfo } from '@/store/userInfo'
const routes = [
  {
    path: '/login',
    name: 'login',
    component: () => import('@/views/login/index.vue'),
  },
  {
    path: '/',
    name: 'mainPage',
    redirect: { name: 'home' },
    component: () => import('@/views/tabbar/index.vue'),
    children: [
      {
        path: '/home',
        name: 'home',
        component: () => import('@/views/mainPages/home/index.vue'),
      },
      {
        path: '/classRoom',
        name: 'classRoom',
        component: () => import('@/views/mainPages/classRoom/index.vue'),
      },
      {
        path: '/myHome',
        name: 'myHome',
        component: () => import('@/views/mainPages/myHome/index.vue'),
      },
    ],
  },
  {
    path: '/noteList',
    name: 'note',
    component: () => import('@/views/noteList/index.vue'),
  },
  {
    path: '/score',
    name: 'score',
    component: () => import('@/views/score/index.vue'),
  },
  {
    path: '/homeWork',
    name: 'homeWork',
    component: () => import('@/views/homeWork/index.vue'),
  },
  {
    path: '/checkIn',
    name: 'checkIn',
    component: () => import('@/views/checkIn/index.vue'),
  },
  {
    path: '/locker',
    name: 'locker',
    component: () => import('@/views/locker/index.vue'),
  },
  {
    path: '/askLeave',
    name: 'askLeave',
    component: () => import('@/views/askLeave/index.vue'),
  },
  {
    path: '/familyPhone',
    name: 'familyPhone',
    component: () => import('@/views/familyPhone/index.vue'),
  },
  {
    path: '/familyMessage',
    name: 'familyMessage',
    component: () => import('@/views/familyMessage/index.vue'),
  },
  {
    path: '/classPhoto',
    name: 'classPhoto',
    component: () => import('@/views/classPhoto/index.vue'),
  },
  {
    path: '/studentPhotoExplain',
    name: 'studentPhotoExplain',
    component: () => import('@/views/classPhoto/studentPhotoExplain/index.vue'),
  },
  {
    path: '/timeTable',
    name: 'timeTable',
    component: () => import('@/views/timeTable/index.vue'),
  },
  {
    path: '/topic',
    name: 'topic',
    component: () => import('@/views/topic/index.vue'),
  },
  {
    path: '/topicDetail',
    name: 'topicDetail',
    component: () => import('@/views/topicDetail/index.vue'),
  },
  {
    path: '/publish',
    name: 'publish',
    component: () => import('@/views/publish/index.vue'),
  },
  {
    path: '/myPermission',
    name: 'myPermission',
    component: () => import('@/views/myPermission/index.vue'),
  },
  {
    path: '/agreeMent',
    name: 'agreeMent',
    component: () => import('@/views/agreeMent/index.vue'),
  },
  {
    path: '/studentPhoto',
    name: 'studentPhoto',
    component: () => import('@/views/studentPhoto/index.vue'),
  },
  {
    path: '/aboutUs',
    name: 'aboutUs',
    component: () => import('@/views/aboutUs/index.vue'),
  },
  {
    path: '/setting',
    name: 'setting',
    component: () => import('@/views/setting/index.vue'),
  },
]

const router = createRouter({
  history: createWebHashHistory(import.meta.env.BASE_URL),
  routes: routes,
})
router.beforeEach(async (to, from, next) => {
  if (to.name == 'login') {
    next()
  } else {
    const user = userInfo()
    if (user.studentName && user.avatar && user.classes) {
      next()
    } else {
      await user.getUserInfo()
      if (user.studentName && user.avatar && user.classes) {
        next()
      } else {
        next('/login')
      }
    }
  }
})
export default router
