// import { defineStore } from 'pinia'
// import { ref } from 'vue'
import request from '../request/index'

export const userInfo = defineStore('userInfo', () => {
  const studentName = ref('')
  const avatar = ref('')
  const classes = ref('')
  const studentId = ref('')
  async function getUserInfo() {
    const result = await request('/api/userInfo', 'get')
    if (result.status !== 200) {
    } else {
      console.log(result.data)
      studentId.value = result.data.data.id
      studentName.value = result.data.data.name
      avatar.value = result.data.data.avatar
      classes.value = result.data.data.class
    }
  }
  return { studentName, avatar, classes, getUserInfo, studentId }
})
